"use client";

import { useState, useCallback, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Copy, RefreshCw, Shield, Settings, Check, Lock } from "lucide-react";
import Confetti from "@/components/animations/Confetti";
import Image from "next/image";

interface PasswordOptions {
  length: number;
  includeUppercase: boolean;
  includeLowercase: boolean;
  includeNumbers: boolean;
  includeSymbols: boolean;
  googleWorkspaceCompatible: boolean;
}

export default function PasswordGenerator() {
  const [password, setPassword] = useState("");
  const [copied, setCopied] = useState(false);
  const [isGenerating, setIsGenerating] = useState(false);
  const [mounted, setMounted] = useState(false);
  const [showConfetti, setShowConfetti] = useState(false);

  const [options, setOptions] = useState<PasswordOptions>({
    length: 14,
    includeUppercase: true,
    includeLowercase: true,
    includeNumbers: true,
    includeSymbols: true,
    googleWorkspaceCompatible: false,
  });

  useEffect(() => {
    setMounted(true);
  }, []);

  const generatePassword = useCallback(
    async (showLoading = true, triggerConfetti = false) => {
      if (showLoading) setIsGenerating(true);

      // Simulation d'un délai pour l'animation
      await new Promise((resolve) => setTimeout(resolve, 500));

      const uppercase = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
      const lowercase = "abcdefghijklmnopqrstuvwxyz";
      const numbers = "0123456789";
      const symbols = options.googleWorkspaceCompatible
        ? "!@#$%^&*()-_=+[]{}|;:,.<>?"
        : "!@#$%^&*()-_=+[]{}|;:,.<>?/~`";

      let chars = "";
      if (options.includeUppercase) chars += uppercase;
      if (options.includeLowercase) chars += lowercase;
      if (options.includeNumbers) chars += numbers;
      if (options.includeSymbols) chars += symbols;

      if (!chars) chars = lowercase;

      let result = "";
      if (typeof window !== "undefined" && window.crypto) {
        const array = new Uint8Array(options.length);
        window.crypto.getRandomValues(array);
        for (let i = 0; i < options.length; i++) {
          result += chars[array[i] % chars.length];
        }
      } else {
        for (let i = 0; i < options.length; i++) {
          result += chars[Math.floor(Math.random() * chars.length)];
        }
      }

      setPassword(result);
      if (showLoading) setIsGenerating(false);

      if (triggerConfetti) {
        setShowConfetti(true);
      }
    },
    [options]
  );

  const handleOptionChange = useCallback(
    (newOptions: Partial<PasswordOptions>) => {
      setOptions((prev) => {
        const updatedOptions = { ...prev, ...newOptions };

        if (password) {
          setTimeout(() => generatePassword(false, false), 0);
        }

        return updatedOptions;
      });
    },
    [password, generatePassword]
  );

  const copyToClipboard = async () => {
    if (!password || typeof window === "undefined") return;

    try {
      if (navigator.clipboard) {
        await navigator.clipboard.writeText(password);
      } else {
        // Fallback pour les navigateurs plus anciens
        const textArea = document.createElement("textarea");
        textArea.value = password;
        document.body.appendChild(textArea);
        textArea.select();
        document.execCommand("copy");
        document.body.removeChild(textArea);
      }

      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (err) {
      console.error("Erreur lors de la copie:", err);
    }
  };

  if (!mounted) {
    return <div className="animate-pulse h-96 bg-card rounded-xl"></div>;
  }

  return (
    <div className="w-full max-w-4xl mx-auto">
      {/* Header avec logo intégré */}
      <div className="bg-gradient-to-br from-card via-card to-card/80 rounded-2xl shadow-2xl border border-border/50 overflow-hidden">
        {/* En-tête avec logo */}
        <div className="bg-gradient-to-r from-primary/10 via-primary/5 to-transparent p-6 border-b border-border/30">
          <div className="flex items-center justify-center space-x-4">
            <div className="relative w-16 h-16 flex items-center justify-center">
              <Image
                src="/logo-mysecurepassword.png"
                alt="MySecurePassword Logo"
                width={64}
                height={64}
                className="object-contain"
                priority
              />
            </div>
            <div className="text-center">
              <h1 className="text-2xl font-bold text-foreground">
                MySecurePassword
              </h1>
              <p className="text-sm text-muted-foreground">
                Générateur sécurisé Google Workspace
              </p>
            </div>
            <div className="flex items-center space-x-1 px-2 py-1 bg-accent/20 border border-accent/30 rounded-lg">
              <span className="text-xs text-accent-foreground">🇫🇷</span>
              <span className="text-xs font-medium text-accent-foreground">
                RGPD
              </span>
            </div>
          </div>
        </div>

        {/* Contenu principal */}
        <div className="p-6 space-y-6">
          {/* Section mot de passe généré */}
          <div className="space-y-4">
            <div className="flex items-center space-x-2">
              <Shield className="w-5 h-5 text-primary" />
              <Label className="text-lg font-semibold text-foreground">
                Mot de passe sécurisé
              </Label>
            </div>

            <div className="relative">
              <Input
                type="text"
                value={password}
                readOnly
                placeholder="Cliquez sur 'Générer' pour créer un mot de passe sécurisé"
                className="w-full h-14 text-lg font-mono bg-background border-2 border-border/50 text-foreground placeholder:text-muted-foreground focus:border-primary focus:ring-2 focus:ring-primary/20 transition-all duration-200"
              />
              {password && (
                <Button
                  onClick={copyToClipboard}
                  variant="ghost"
                  size="sm"
                  className="absolute right-2 top-1/2 transform -translate-y-1/2 h-10 px-3 hover:bg-primary/10"
                >
                  {copied ? (
                    <Check className="w-4 h-4 text-green-500" />
                  ) : (
                    <Copy className="w-4 h-4 text-muted-foreground" />
                  )}
                </Button>
              )}
            </div>

            <Button
              onClick={() => generatePassword(true, true)}
              disabled={isGenerating}
              className="w-full h-14 text-lg font-semibold bg-gradient-to-r from-primary to-primary/80 hover:from-primary/90 hover:to-primary/70 text-primary-foreground shadow-lg hover:shadow-xl transition-all duration-300"
            >
              {isGenerating ? (
                <RefreshCw className="w-5 h-5 mr-2 animate-spin" />
              ) : (
                <Shield className="w-5 h-5 mr-2" />
              )}
              {isGenerating
                ? "Génération..."
                : "Générer un mot de passe sécurisé"}
            </Button>
          </div>

          {/* Configuration */}
          <div className="space-y-4">
            <div className="flex items-center space-x-2">
              <Settings className="w-5 h-5 text-primary" />
              <Label className="text-lg font-semibold text-foreground">
                Configuration
              </Label>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              {/* Longueur */}
              <div className="space-y-3">
                <Label className="text-sm font-medium text-foreground">
                  Longueur: {options.length} caractères
                </Label>
                <input
                  type="range"
                  min="8"
                  max="64"
                  value={options.length}
                  onChange={(e) =>
                    handleOptionChange({ length: parseInt(e.target.value) })
                  }
                  className="w-full h-2 bg-border rounded-lg appearance-none cursor-pointer slider-primary"
                  aria-label="Longueur du mot de passe"
                  title={`Longueur du mot de passe: ${options.length} caractères`}
                />
              </div>

              {/* Options de caractères */}
              <div className="space-y-3">
                <Label className="text-sm font-medium text-foreground">
                  Types de caractères
                </Label>
                <div className="grid grid-cols-2 gap-2">
                  {[
                    {
                      key: "includeUppercase",
                      label: "Majuscules (A-Z)",
                      desc: "ABC...",
                    },
                    {
                      key: "includeLowercase",
                      label: "Minuscules (a-z)",
                      desc: "abc...",
                    },
                    {
                      key: "includeNumbers",
                      label: "Chiffres (0-9)",
                      desc: "123...",
                    },
                    {
                      key: "includeSymbols",
                      label: "Symboles (!@#)",
                      desc: "!@#...",
                    },
                  ].map(({ key, label, desc }) => (
                    <label
                      key={key}
                      className="flex items-center space-x-2 p-2 rounded-lg hover:bg-accent/10 cursor-pointer transition-colors"
                    >
                      <input
                        type="checkbox"
                        checked={
                          options[key as keyof PasswordOptions] as boolean
                        }
                        onChange={(e) =>
                          handleOptionChange({ [key]: e.target.checked })
                        }
                        className="w-4 h-4 text-primary border-border rounded focus:ring-primary focus:ring-2"
                      />
                      <div>
                        <div className="text-sm font-medium text-foreground">
                          {label}
                        </div>
                        <div className="text-xs text-muted-foreground">
                          {desc}
                        </div>
                      </div>
                    </label>
                  ))}
                </div>
              </div>
            </div>

            {/* Google Workspace */}
            <div className="p-4 bg-accent/10 border border-accent/20 rounded-xl">
              <label className="flex items-start space-x-3 cursor-pointer">
                <input
                  type="checkbox"
                  checked={options.googleWorkspaceCompatible}
                  onChange={(e) =>
                    handleOptionChange({
                      googleWorkspaceCompatible: e.target.checked,
                    })
                  }
                  className="w-4 h-4 text-primary border-border rounded focus:ring-primary focus:ring-2 mt-1"
                />
                <div>
                  <div className="text-sm font-medium text-foreground">
                    Mode Google Workspace
                  </div>
                  <div className="text-xs text-muted-foreground mt-1">
                    Exclut les caractères problématiques pour une compatibilité
                    maximale avec Google Workspace
                  </div>
                </div>
              </label>
            </div>
          </div>
        </div>
      </div>

      {/* Confetti */}
      <Confetti
        isActive={showConfetti}
        onComplete={() => setShowConfetti(false)}
      />
    </div>
  );
}
